.container{
    padding: 50px 100px;
}
i{
    font-family: "FontAwesome";
}
.head{
    display: flex;
    justify-content: space-between;
    border: 1px red solid;
    height: 60px; align-items: center;
    position: relative;
}

.main{
    border: 1px black solid;
    display: flex;
    justify-content: space-between;
    position: relative;
}

.side{
    padding: 20px;
    border: 1px black solid;
    flex: 1;
}
.main-info{
    border: 1px black solid;
    flex: 3;
    text-align: center;
}
.main-serving{
    display: flex;
    justify-content: space-around;
}
.main-img-title, .main-serving, .ingredients, footer{
    border: 1px black solid;
    width: 100%;
}

.aside-links{
    width: 100%;
    display: flex; flex-direction: row;
    align-items: center; justify-content: space-evenly;
    border: 1px solid blue;
}
.aside-links img{
    width: 60px; height: 70px;
    border-radius: 180px;
}

.recipe-img{
    width: 100%;
    height: 300px;
}

.bookmarked{
    background-color: red;
}

.bookmark-block{
    height: 60px;
}
.bookmark-show-btn{
    height: 100%;
}

.bookmark-modal{
    padding: 20px 0;
    z-index: 2;
    width: 350px; height: auto;
    position: absolute;
    top: 60px; right: 0;
    color: black;
    background-color: #FFF;
    display: none;
}
.bookmark-link{
    display: flex; justify-content: space-evenly; align-items: center;
    width: 100%; height: 100px; margin-bottom: 15px;
}
.bookmark-link img{
    width: 90px; height: 90px;
    border-radius: 180px;
}
.bookmark-link:hover{
    background-color: rgba(0, 0, 0, 0.1);
}

.bookmark-block:hover .bookmark-modal{
    display: flex; flex-direction: column; justify-content: space-evenly;
}

/* MODAL FORM */

.form-container {
    position: absolute; top: 0; right: 0; left: 0;
    z-index: 3;
    backdrop-filter: blur(4px);
    background-color: rgba(0, 0, 0, 0.6);
    width: 100vw; height: 100vh;
    display: flex; align-items: center;
    justify-content: center;
}

.add-form{
    padding: 50px;
    display: flex; flex-direction: column;
    justify-content: center; align-items: center;
    background-color: white;
    position: relative;
}
.add-recipe-form{
    display: flex; justify-content: space-evenly;
    width: 1000px;
}

.recipe-data{
    width: 420px;
}
.recipe-data > div{
    display: flex; justify-content: space-between;
}
.add-ingredients{
    width: 420px;
}
.add-ingredients > div{
    display: flex; justify-content: space-between;
}

.add-recipe-form input{
    margin-bottom: 15px;
    font-size: 1.4em;
    padding: 5px 10px; 
    border-radius: 5px;
    border: 1px solid rgba(0, 0, 0, 0.3);
}

.upload-btn{
    background: linear-gradient(to right bottom,#fbdb89,#f48982);
    color: white; font-size: 1.3em;
    border: none; padding: 10px 15px; 
    border-radius: 20px;
}

.add-recipe-form label{
    font-size: 1.3em;
    position: relative; top: 5px;
}

.form-hidden{
    display: none;
}

.exit-btn{
    position: absolute; top: 0; right: 0;
    font-weight: bold; font-size: 1.5em;
    cursor: pointer;
}